<template>
<div>
  <H1>雅居乐花园小区"四返"人员信息统计</H1>
  <p class="text">1.两省四市返回人员共{{home}}户,{{people}}人.</p>
  <p class="text">(目前正在隔离{{isolateHome}}户{{isolatePeople}}人.其中湖北{{hubeiHome}}户{{hubeiPeople}}人.)</p>
  <p class="text">2.其他四返人员共{{otherHome}}户{{otherPeople}}人.</p>
  <p class="text">(目前自行隔离{{otherIsolateHome}}户{{otherIsolatePeople}}人,解除隔离{{freeOtherHome}}户{{freeOtherPeople}}人.)</p>
  <p class="text">3.网格化管理人员(市码扫码人员)共{{qrcodeHome}}户{{qrcodePeople}}人.</p>
  <p class="text">(目前扫码隔离{{isolateQrcodeHome}}户{{isolateQrcodePeople}}人,解除{{freeQrcodeHome}}户{{freeQrcodePeople}}人.)</p>
</div>
</template>

<script>
    export default {
        name: "Statistics",
      data () {
        return {

          home:33,
          people:49,
          isolateHome:2,
          isolatePeople:5,
          hubeiHome:1,
          hubeiPeople:4,
            otherHome:209,
              otherPeople:318,
        otherIsolateHome:83,
        otherIsolatePeople:147,
        freeOtherHome:126,
        freeOtherPeople:171,
        qrcodeHome:42,
        qrcodePeople:54,
        isolateQrcodeHome:39,
        isolateQrcodePeople:49,
        freeQrcodeHome:3,
        freeQrcodePeople:5

        }
      }
      ,
      methods: {
        loadData () {

          var that = this;

          this.$axios.get('https://www.geckos.top/api/statistics')
            .then(res => {

              if (res.data!=null) {

                var stat = res.data;

                that.home = stat.rooms;
                that.people= stat.people;
                that.qrcodeHome = stat.qrcodeRooms;
                that.qrcodePeople = stat.qrcodePeople;


              }

        })
        }
      },
      created () {
        this.loadData()
      },
      mounted () {

      }
    }
</script>

<style scoped>
  .text{
    font-size:1.2em;
    font-weight: bold;
    text-align: left;
  }

</style>



