<style>
  .echarts {
    width: 100%;
    height: 100%;
  }
  .title{
    text-align: center;
    font-size:1.5em;
    font-weight: bold;
    margin-bottom: 10px;
  }
  .time{
    color: darkgray;
    font-size:1em;
    text-align: center;
  }
  .charts{
    margin-top:20px;
  }
  .table{
    width: 100%;
    margin-top:20px;
  }
  .text-all{
    color: darkblue;
    font-size:0.8em;
  }
  .text-all1{
    color: darkblue;
    font-size:1.5em;
    font-weight: bold;
  }
  .text-isolate{
    color: chocolate;
    font-size:0.8em;
  }
  .text-isolate1{
    color: chocolate;
    font-size:1.5em;
    font-weight: bold;
  }
  .text-free{
    color: darkgreen;
    font-size:0.8em;
  }
  .text-free1{
    color: darkgreen;
    font-size:1.5em;
    font-weight: bold;
  }
  .text-new-isolate{
    color: chocolate;
    font-size:0.8em;

  }
  .text-new-isolate1{
    color: chocolate;
    font-size:1.5em;
    font-weight: bold;
  }
  .text-new-free{
    color: darkgreen;
    font-size:0.8em;
  }
  .text-new-free1{
    color: darkgreen;
    font-size:1.5em;
    font-weight: bold;
  }
</style>

<template>
  <div>
    <div class="title">雅居乐花园"四返"人员动态信息</div>
    <div class="time">(数据更新时间:{{date}})</div>
    <table class="table">
      <tr>
        <th class="text-all">总人数</th>
        <th class="text-isolate">隔离人数</th>
        <th class="text-free">解除人数</th>
        <th class="text-new-isolate">新增隔离</th>
        <th class="text-new-free">新增解除</th>
      </tr>
      <tr>
        <td class="text-all1">{{allPeople}}</td>
        <td class="text-isolate1">{{allIsolate}}</td>
        <td class="text-free1">{{allFree}}</td>
        <td class="text-new-isolate1">{{newIsolate}}</td>
        <td class="text-new-free1">{{newFree}}</td>
      </tr>
    </table>
    <div class="charts">
      <chart ref="region" :auto-resize="true" style="width:100%;" :options="region"/>
      <chart ref="isolate"  :auto-resize="true" style="width:100%;" :options="isolate"/>
      <chart ref="line"  :auto-resize="true"  style="width:100%;" :options="line"/>
    </div>
  </div>
</template>

<script>
  import 'echarts/lib/chart/bar'
  import 'echarts/lib/chart/line'
  import 'echarts/lib/component/tooltip'
  import 'echarts/lib/component/polar'
  import 'echarts/lib/component/legend'
  import 'echarts/lib/component/title.js'
  import 'echarts/lib/chart/pie'


  export default {
    name:"HelloWorld",
    data () {
      return {
        date:null,
        allPeople:0,
        allIsolate:0,
        allFree:0,
        newIsolate:0,
        newFree:0,
        region: {
          title : {
            text: '"四返"人员始发地区分布',
            subtext: '动态数据',
            x:'left'
          },
          tooltip : {
            trigger: 'item',
            formatter:"{a} <br/>{b} : {c}人 ({d}%)"
          },
          color:['crimson', 'darkorange','cadetblue'],
          legend: {
            show: true,
            orient: 'horizontal',
            bottom: '20px',
            data: ['湖北省','一省四市','其他地区']
          },
          series : [
            {
              name: '此地区人员数量',
              type: 'pie',
              radius : '50%',
              center: ['50%', '50%'],
              label: {
                formatter: '{b}：{c}人 '}
              ,
              data:[
              ],
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        },
        isolate: {
          title : {
            text: '当前隔离与解除隔离人员占比',
            subtext: '动态数据',
            x:'left'
          },
          tooltip : {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c}人 ({d}%)"
          },
          color:['crimson', 'darkorange','cadetblue','forestgreen'],
          legend: {
            show: true,
            orient: 'horizontal',
            bottom: '20px',
            data: ['湖北隔离人数','一省四市隔离人数','其他隔离人数','解除隔离人数']
          },
          series : [
            {
              name: '此类人员数量',
              type: 'pie',
              radius : '50%',
              center: ['50%', '50%'],
              label: {
                formatter: '{b}：{c}人 '}
                ,
              data:[
              ],
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        },
        line:{
          title : {
            text: '隔离人数走势',
            subtext: '动态数据',
            x:'left'
          },
          xAxis: {
            type: 'category',
            data: []
          },
          yAxis: {
            type: 'value'
          },
          series: [{
            data: [],
            label: {
              show:true,
              position:'top'
            }
            ,
            type: 'line'
          }]
        }

      }
    }
  ,
  methods: {
    loadData () {

      var that = this;

      this.$axios.get('https://www.geckos.top/api/peopleinfos')
        .then(res => {
          if (res.data!=null) {

            var peopleInfos = res.data;

            that.date = peopleInfos.date;

            that.allPeople = peopleInfos.hubei+peopleInfos.hotCity+peopleInfos.otherCity;
            that.allIsolate = peopleInfos.hubeiIsolate+peopleInfos.hotCityIsolate+peopleInfos.otherIsolate;
            that.allFree = peopleInfos.notIsolate;
            ///that.newIsolate = peopleInfos.newIsolate;
            //that.newFree = peopleInfos.newFree;

            that.region.series[0].data=[
              {value:peopleInfos.hubei, name:'湖北省'},
              {value:peopleInfos.hotCity, name:'一省四市'},
              {value:peopleInfos.otherCity, name:'其他地区'}
            ];

            that.isolate.series[0].data=[
              {value:peopleInfos.hubeiIsolate, name:'湖北隔离人数'},
              {value:peopleInfos.hotCityIsolate, name:'一省四市隔离人数'},
              {value:peopleInfos.otherIsolate, name:'其他隔离人数'},
              {value:peopleInfos.notIsolate, name:'解除隔离人数'}
            ];

            that.line.xAxis.data= peopleInfos.dayliyDate;
            that.line.series[0].data=peopleInfos.dayliyIsolateNum;

            //that.$refs.region.mergeOptions(that.region);
          }
        })

    }
  },
  created () {
    this.loadData()
  },
  mounted () {

  }
  }
</script>



